.account_update_popup {
  margin: 0 auto;
  padding: 10px;
  width: 1520px;
  position: relative;
  background: whitesmoke;
  box-shadow: 5px 4px 8px -1px rgba(10, 5, 1, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transform: translate(-4%,-45%);

  .popup_block {
    display: flex;
    align-items: center;
    gap: 5px;

  .popup_title {
    font-size: 1rem;
    font-family: 'Roboto';
    color: blue;
  }
    .error_title {
      font-size: 1rem;
      font-family: 'Roboto';
      color: red;
    }
  }
}