.products_popup {
  width: auto;
  padding: 0;
  margin: 0 auto;
  position: absolute;
  top: 158px;
  transform: translateX(-35%);
  z-index: 100 !important;

  .products_column {

    width: 100%;
    height: 100%;
    padding: 5px 5px;
    border: 2px solid whitesmoke;
    flex-direction: column !important;;
    align-items: center !important;
    justify-content: center !important;
    gap: 10px !important;
    background: whitesmoke;
    box-shadow: 5px 4px 8px -1px rgba(10, 5, 1, 0.3);
    cursor: auto;
    transition: all .2s ease !important;

    .products_item {
      padding: 0;
      margin: 0 auto;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      gap: 5px;
      font-family: "Roboto";
      color: rgb(34, 37, 49);
      font-size: 0.9rem;
      font-weight: 600;
      cursor: pointer;
      text-align: center;

      &:hover {
        color: #0974d8;
      }
    }
  }
}


@media (max-width: 1325px) {

  .products_popup {

    top: 158px !important;
    transform: translateX(-25%);
  }
}


@media (max-width: 1104px) {

  .products_popup {

    top: 100px !important;
    transform: translateX(50%);

    .products_column {
      padding: 5px 5px;
    }
  }
}

@media (max-width: 700px) {

  .products_popup {
    position: inherit;
    top: 0px !important;
    transform: translateX(0%) !important;

    .products_column {
      width: 160px;
      padding: 5px 2px !important;

      .products_item {
        justify-content: center !important;
      }
    }
  }
}