.community_popup {
  width: auto;
  padding: 0;
  margin: 0 auto;
  position: absolute;
  top: 158px;
  transform: translateX(-10%)!important;
  z-index: 100!important;


  .community_column {

    width: 100%;
    height: 100%;
    padding: 15px 25px!important;
    border: 2px solid whitesmoke;
    flex-direction: column!important;
    align-items: center!important;
    justify-content: center!important;
    gap: 20px!important;

    background: whitesmoke;
    box-shadow: 5px 4px 8px -1px rgba(10, 5, 1, 0.3);
    cursor: auto;

    .community_column_item {
      padding: 0;
      margin: 0 auto;
      display: flex;
      flex-direction: row;
      align-items: center;
      font-family: "Roboto";
      color: rgb(34, 37, 49);
      font-size: 0.9rem;
      font-weight: 600;
      cursor: pointer;

      &:hover {
        color: #0974d8;
      }
    }
  }
}
@media (max-width: 1330px) {

  .community_popup {
    top: 145px!important;




  }
}
@media (max-width: 1325px) {

  .community_popup {
    top: 158px!important;

  }
}

@media (max-width: 1104px) {

  .community_popup {

    top: 100px!important;
    transform: translateX(90%)!important;

    .community_column {
      padding: 5px 5px;
    }
  }
}

@media (max-width: 700px) {

  .community_popup {
    position: inherit;
    top: 0px !important;
    transform: translateX(0%) !important;

    .community_column {
      width: 160px;
      gap: 2px!important;
      padding: 5px 2px !important;

      .community_column_item {
        justify-content: center !important;
      }
    }
  }
}