.coin_info_popup {
  padding-top: 5px;
  margin-bottom: 10px;
  width: 350px;
  height: 100%;
  background: whitesmoke;
  box-shadow: 5px 4px 8px -1px rgba(10, 5, 1, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;


  h2 {
    width: 100%;
    padding: 7px 9px;
    font-family: 'Roboto';
    color: black;
    font-weight: 500;
    font-size: 1rem;
  }

  p {
    margin-bottom: 2px;
    padding: 7px 9px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.43;
    font-family: 'Roboto'
  }
}


@media screen and (max-width: 1005px) {

  .coin_info_popup {
    height: 100%;

    h2 {

    }
    p {
      width: 100%;
      font-size: 0.8rem;
    }
  }
}


@media screen and (max-width: 650px) {

  .coin_info_popup {
    width: 250px!important;

    h2 {
      font-size: 0.7rem!important;
    }
    p {

      font-size: 0.6rem!important;
    }
  }
}